export default {
	data() {
		return {
			
		}
	},
	methods: {
		getUserInfo(res){
			let rawData=res.detail.rawData;
			if(rawData){
				//授权成功
				//switchTab跳转到tabBar页面,并通过Storage传值
				wx.setStorageSync('userInfo',rawData)
				uni.reLaunch({
					url:"/pages/personal/personal?userInfo=" + rawData
				})
			}else{
				//授权失败
				uni.showToast({
					title:"授权失败,请允许",
					icon:"none"
				})
			}
		}
	}
}
